			border-top: 4px solid #fff; }
		&:hover {
			cursor: pointer; } }
	.lang__item {
		a {
			position: relative;
			&::after {
				position: absolute;
				bottom: -2px;
				left: 0;
				content: '';
				display: block;
				height: 1px;
				width: 100%;
				background-color: #fff;
				transform: scale(0); }
			&:hover {
