/* ===========================Global_class========================*/
.leaves-green {
  padding-bottom: 45px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.leaves-green::before {
  content: '';
  margin-right: 30px;
  width: 40px;
  height: 40px;
  background: url("../img/svg/leaves-w.svg") center center no-repeat;
}

.js-no-valid {
  border-bottom: 1px solid red !important;
}

.menu-section.active-js {
  opacity: 1;
  visibility: visible;
}

.menu-section {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(153, 204, 51, 0.9);
  z-index: 9998;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}

.menu-section .menu-block-top {
  height: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../img/bg-menu.jpg") center center no-repeat;
  background-size: cover;
  z-index: -2;
}

.menu-section .menu-block-top .menu__word {
  padding-top: 80px;
  position: relative;
  font-size: 30px;
  font-weight: 300;
  line-height: 60px;
  text-transform: uppercase;
  letter-spacing: 2.1px;
  color: #fff;
  text-transform: uppercase;
}

.menu-section .menu-block-top .menu__word::before {
  position: absolute;
  top: 78%;
  left: -55px;
  transform: translateY(-50%);
  content: '';
  display: inline-block;
  width: 40px;
  height: 34px;
  background: url("../img/svg/leaves-w-w.svg") center center no-repeat;
}

.menu-section .menu-block-top::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: -1;
}

.menu-section .menu-block-bottom {
  height: 50%;
  position: relative;
  background: url("../img/home/home-bg.jpg") bottom center no-repeat;
  background-size: cover;
  z-index: -2;
}

.menu-section .menu-block-bottom::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(85, 136, 15, 0.85);
  z-index: -1;
}

.menu-section .menu-block-bottom .menu-wrap {
  font-weight: 400;
  line-height: 42px;
  font-size: 20px;
  letter-spacing: 0.2px;
}

.menu-section .menu-block-bottom .menu-wrap a {
  color: #fff;
}

.menu-section .menu-block-bottom .menu-wrap .header-menu {
  display: flex;
  justify-content: space-between;
  transform: translateY(-70px);
  z-index: 20;
}

.menu-section .menu-block-bottom .menu-wrap .header-menu .has-children a.menu__title {
  margin-bottom: 65px;
  display: block;
  font-size: 20px;
  text-transform: uppercase;
}

.menu-section .menu-block-bottom .menu-wrap .header-menu .has-children ul.sub-menu {
  list-style: square outside;
}

.menu-section .menu-block-bottom .menu-wrap .header-menu .has-children ul.sub-menu li {
  position: relative;
  padding-left: 35px;
}

.menu-section .menu-block-bottom .menu-wrap .header-menu .has-children ul.sub-menu li::before {
  content: '';
  width: 3px;
  height: 3px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: #fff;
}

.menu-section .menu-block-bottom .menu-wrap .header-menu .has-children ul.sub-menu li a {
  font-size: 16px;
  letter-spacing: 0.16px;
  font-weight: 300;
  position: relative;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0.4px;
}

.menu-section .menu-block-bottom .menu-wrap .header-menu .has-children ul.sub-menu li a::after {
  position: absolute;
  bottom: -8px;
  left: 0;
  content: '';
  display: block;
  height: 1px;
  width: 100%;
  background-color: #fff;
  transform: scale(0);
}

.menu-section .menu-block-bottom .menu-wrap .header-menu .has-children ul.sub-menu li a:hover::after {
  transition: 0.4s;
  transform: scale(1);
}

@keyframes firstLeft {
  from {
    opacity: 0;
    transform: translate(-60px, -60px);
    z-index: 12;
  }
  to {
    transform: translate(0%, 0%);
    z-index: 10;
    opacity: 1;
  }
}

.firstLeft {
  animation: firstLeft 0.9s forwards;
  visibility: visible !important;
}

@keyframes firstRight {
  from {
    transform: translate(0, 0);
    z-index: 10;
    opacity: 1;
  }
  to {
    transform: translate(-60px, -60px);
    z-index: -1;
    opacity: 0;
  }
}

.firstRight {
  animation: firstRight 0.6s forwards;
  visibility: visible !important;
}

@keyframes twoLeft {
  from {
    transform: translate(0%, 0%);
    z-index: -1;
    opacity: 1;
  }
  to {
    transform: translate(120px, 120px);
    z-index: -1;
    opacity: 1;
  }
}

.twoLeft {
  animation: twoLeft 0.9s forwards;
  visibility: visible !important;
  position: relative;
}

.twoLeft::before {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}

@keyframes twoRight {
  from {
    transform: translate(120px, 120px);
    z-index: -2;
    opacity: 0;
  }
  to {
    transform: translate(0%, 0%);
    z-index: -1;
    opacity: 1;
  }
}

.twoRight {
  animation: twoRight 0.6s forwards;
  transition: all  0.7s;
  visibility: visible !important;
}

@keyframes threeLeft {
  from {
    transform: translate(120px, 120px);
    z-index: -1;
    opacity: 1;
  }
  to {
    transform: translate(30%, 40%);
    z-index: -2;
    opacity: 0;
  }
}

.threeLeft {
  animation: threeLeft 0.9s forwards;
  visibility: visible !important;
}

@keyframes threeRight {
  from {
    transform: translate(200px, 200px);
    z-index: -1;
    opacity: 1;
  }
  to {
    transform: translate(120px, 120px);
    z-index: -2;
    opacity: 1;
  }
}

.threeRight {
  animation: threeRight 0.6s forwards;
  visibility: visible !important;
  position: relative;
}

.threeRight::before {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}

@keyframes fourLeft {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.fourLeft {
  animation: fourLeft 0.7s forwards;
  visibility: visible !important;
}

@keyframes fourRight {
  from {
    opacity: 0;
    transform: translate(-6%, -6%);
    z-index: 7;
  }
  to {
    opacity: 1;
    transform: translate(-3%, -3%);
    z-index: 8;
  }
}

.fourRight {
  animation: firstRight 0.7s forwards;
  visibility: visible !important;
}

