body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.65;
  font-family: "GothamPro", sans-serif;
  overflow-x: hidden;
  color: #606060;
  background-color: #fff;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

b, strong {
  font-weight: bold;
}

img {
  width: 100%;
}

img:focus {
  outline: none;
}

a {
  text-decoration: none;
}

a:hover {
  cursor: pointer;
}

button {
  border: none;
  text-decoration: none;
}

button:hover {
  cursor: pointer;
}

.button {
  margin: 0 auto;
  display: block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #568F0A;
  padding: 15px 45px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 3px;
  border-radius: 2px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .1s ease;
  cursor: pointer;
}

.button::after {
  transition: background-color .2s ease;
  position: absolute;
  content: '';
  height: 4px;
  bottom: 0;
  width: 100%;
  background-color: black;
  opacity: .18;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  left: 0;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: #64a70c;
}

.button:hover::after {
  opacity: .22;
}

.button:active {
  background-color: #487708;
}

.button:active::after {
  opacity: .32;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
